import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { NewsNoticeComponent } from './news-notice/news-notice.component';
import { NewsNoticeDetailComponent } from './news-notice-detail/news-notice-detail.component';

const routes: Routes = [
  {
    path: '',
    component: NewsNoticeComponent
  },
  {
    path: 'detail',
    component: NewsNoticeDetailComponent
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NewsNoticeRoutingModule { }
